.room-code {
  border-radius: 8px;
  overflow: hidden;

  background: #FFF;
  border: 1px solid #835afd;
  cursor: pointer;

  display: flex;

  div {
    background: #835afd;
    padding: 0 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px
  }

  span {
    display: block;
    align-self: center;
    flex: 1;
    padding: 0 16px 0 12px;
    width: 230px;
    font-size: 1.4rem;
    font-weight: 500;
  }
}

@media (max-width: 460px) {
  .room-code {

    span {
      content-visibility: hidden;
      padding: 0;
      width: min-content;
    }
  } 
}